import {contentAbout,contentMainPage,contentBlog} from "../scripts/textContents";
import Section from "./Section";

const createSlide=className=>content=>Section({...content,className})

const sectionsAbout=contentAbout.map(createSlide("section-background"));
const sectionsMainPage=contentMainPage.map(createSlide("section-background2"));
const sectionsBlog=contentBlog.map(createSlide("section-background"));

export {sectionsAbout,sectionsMainPage,sectionsBlog,createSlide};