import Header from "./Header";

export default function SliderBody({slides,indexSlide,classNameHeader="",isHeader}){
    return(
        <div className="slider">
            {isHeader && <Header visible={indexSlide===0} className={classNameHeader}/>}
            {slides.map((Slide,i)=>{
                return <Slide key={i} visible={indexSlide===(i+1)}/>
            })}
        </div>
    )
}