import generate from "../scripts/generates";

export default function SliderController({setIndexSlide,slideNumber,indexSlide}){

    const changeSlide=i=>e=>{
        setIndexSlide(i)
    }

    return(
        <div className="conteiner">
            <div className="slider-controller">
                {generate(i=>{
                    console.log(indexSlide===i)
                    return <div key={i}
                                onClick={changeSlide(i)} className={`slider-controller__btn ${indexSlide===i?"activate-btn":""}`}></div>
                },slideNumber+1)}
            </div>
        </div>
    )
}