import {useState} from "react"
import SliderBody from "./SliderBody"
import SliderController from "./SliderController"


export default function Slider({slides=[],classNameHeader="",isHeader=true}){
    const [indexSlide,setIndexSlide]=useState(0)
    return(
        <>
           <SliderController setIndexSlide={setIndexSlide} slideNumber={slides.length} indexSlide={indexSlide}/>
           <SliderBody slides={slides} indexSlide={indexSlide} classNameHeader={classNameHeader} isHeader={isHeader}/> 
        </>
    )
}