import './App.css';
import React,{useRef} from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import NavBar from "./Components/NavBar"
import {pages,pagesGame} from "./scripts/pages"
import Scene from "./Components/Scene"
import useThreeFacade from "./scripts/threeFasade"
import GamePage from "./Components/GamePage"


function App() {
  const ref=useRef(null)
  useThreeFacade(ref)
  return (
    <Router>
      <div className="background" ref={ref}></div>
      <NavBar/>
      <Switch>
        {pagesGame.map(route=>{
           return(
              <Route path={route.url} key={route.url}>
                  <GamePage img={route.img} cost={route.cost} gameName={route.gameName} sectionsContent={route.contents}/>
              </Route>
           )
        })}
        
        {pages.map(route=>{
          return <Route path={route.url} key={route.url}>{route.content}</Route>
        })}
      </Switch>
    </Router>
  );
}

export default App;