import * as THREE from "three"
import createCanvas from "./canvas"
import load from "./loadeModel"
import model from "../model3d/gold_castel.obj"
import {useEffect} from "react"
import createCastle from "./castel"
import textureImg from "../img/texture.jpg"
import textureImg2 from "../img/texture2.jpg"
import textureImg3 from "../img/texture3.jpg"
import textureImg4 from "../img/texture4.jpg"

export default function useThreeFacade(ref){
    useEffect(()=>{
        const canvas=createCanvas(ref)

        const textureLoader = new THREE.TextureLoader();
        const texture = textureLoader.load(textureImg);
        const texture2 = textureLoader.load(textureImg2);
        const texture3 = textureLoader.load(textureImg3);
        const texture4 = textureLoader.load(textureImg4);


        const boxGeometry=new THREE.BoxGeometry(8,8,8);
        const material = [
            new THREE.MeshBasicMaterial({
                map: texture,
                color:0x00ffff
            }),
            new THREE.MeshBasicMaterial({
                map: texture2,
                color:0x666666
            }),
            null,
            null,
            new THREE.MeshBasicMaterial({
                map: texture4,
                color:0x006666
            }),
            new THREE.MeshBasicMaterial({
                map: texture3,
                color:0xff00ff
            })
        ]
        const box = new THREE.Mesh(boxGeometry, material);

        canvas.scene.add(box)
        canvas.camera.position.z = 10
        const animate = function () {
            canvas.renderer.setSize(document.documentElement.clientWidth, document.documentElement.clientHeight);
            canvas.renderer.render(canvas.scene, canvas.camera);
            box.rotation.y+=0.001
            requestAnimationFrame(animate);
        };
        animate();
       
    },[])
}