import Block from "./Block";


export default function Game({visible=true,img,gameName}){
    return(
        <Block visible={visible} className="game-block">
            <div className="game-block__conteiner">
                <img className="game-block__img" src={img} alt="icon"/>
                <h3>{gameName}</h3>
            </div>
        </Block>
    )
}