import Game from "./Game";
import { pagesGame } from "../scripts/pages";
import { Link } from "react-router-dom";

export default function Shop(){
    return(
        <div className="page shop">
            {pagesGame.map(link=>{
                return(
                    <Link to={link.url} key={link.gameName}>
                        <Game visible img={link.img} gameName={link.gameName}/>
                    </Link>
                )
            })}
        </div>
    )
}