import Game from "./Game";
import Button from "./Button";
import Block from "./Block";
import Slider from "./Slider";
import { createSlide } from "./Slides";

const component=(cost)=>({visible})=>(
    <Block visible={visible} className="section">
        <Button>  {cost} </Button>
    </Block>   
)
   

export default function GamePage({img,gameName,cost,sectionsContent=[]}){
    const sections=sectionsContent.map(createSlide("game-block"))
    return(
        <div className="page game-page">
            <Game img={img} gameName={gameName}/>
            <Slider slides={[
                component(cost),
                ...sections
            ]} isHeader={false}/>
        </div>
    )
}