import Block from "./Block";


export default function Section({children,className="",caption,textContents=[]}){
    return ({visible})=>(
        <Block visible={visible} className={"section "+className}>
            <h2>{caption}</h2>
            {textContents.map(textContent=><p className="section__p" key={textContent}>{textContent}</p>)}
            {children}
        </Block>
    )
}